import React from "react";

function Landing({handleBookClick}){
    return (
        <div className='landing-container' id='landing'>
            {/*<div className='landing'>*/}
                <div className='header'>Odzyskaj zdrowie - odzyskaj ycie
                </div>
                <div className='subheader'>Profesjonalna rehabilitacja dla kadego  nowoczesne metody, indywidualne podejcie, skuteczne efekty</div>
                <button className='button' onClick={handleBookClick}>Umw wizyt</button>
            {/*</div>*/}
        </div>
    )
}

export default Landing;