import React, { useContext, useState } from "react";
import { DataContext } from "../App";
import Row from "./Row";
import SearchBar from "./SearchBar";

function Personel() {
    const { employees, setAlertOpened, setAlertData, fetchEmployees } = useContext(DataContext);
    const [addingForm, setAddingForm] = useState(false);
    const [formInputs, setFormInputs] = useState({
        name: '',
        surname: '',
        position: '',
        email: '',
    });
    const [empMod, setEmpMod] = useState({});
    const [filteredEmployees, setFilteredEmployees] = useState([]);

    const handleSearch = (query) => {
        const filtered = employees.filter(employee => {
            const name = employee.name.toLowerCase();
            const surname = employee.surname.toLowerCase();
            const position = employee.position.toLowerCase();
            const email = employee.email.toLowerCase();
            return name.includes(query) || surname.includes(query) || position.includes(query) || email.includes(query);
        });
        setFilteredEmployees(filtered);
    };


    const handleAddServiceClick = () => {
        setAddingForm(!addingForm);
    };

    const handleInputChange = (setState) => (e) => {
        const { name, value } = e.target;
        setState((prevState) => ({
            ...prevState,
            [name]: value,
        }));
    };

    const handleAddingFormSubmit = (e) => {
        e.preventDefault();
        console.log(formInputs);

        fetch(`http://localhost:8090/api/employee/add?name=${encodeURIComponent(formInputs.name)}&surname=${encodeURIComponent(formInputs.surname)}&password=password123&email=${encodeURIComponent(formInputs.email)}&position=${encodeURIComponent(formInputs.position)}`)
            .then(() => {
                setFormInputs({
                    name: '',
                    surname: '',
                    position: '',
                    email: '',
                });
                setAlertOpened(true);
                setAlertData({
                    message: "Nowy pracownik dodany do systemu",
                    color: 'grey'
                });
                fetchEmployees(); // Fetch updated data after adding a new employee
            })
            .catch(error => console.error("Error adding new employee", error));
    };

    const handleEditEmployeeData = (id) => {

        fetch(`http://localhost:8090/api/employee/edit?id=${id}&name=${empMod.name}&surname=${empMod.surname}&email=${empMod.email}&position=${empMod.position}`)
            .then(() => {
                setEmpMod({});
                setAlertOpened(true);
                setAlertData({
                    message: "Dane pracownika zostay zmienione",
                    color: 'grey'
                });
                fetchEmployees(); // Fetch updated data after adding a new employee
            })
            .catch(error => console.error("Error adding new employee", error));
    };

    const handleDeleteEmployee = (id) => {
        fetch(`http://localhost:8090/api/employee/delete?id=${id}`)
            .then(() => {
                setAlertOpened(true);
                setAlertData({
                    message: "Dane pracownika zostay usunite",
                    color: 'grey'
                });
                fetchEmployees(); // Fetch updated data after adding a new employee
            })
            .catch(error => console.error("Error adding new employee", error));
    };



    return (
        <div className='admin-panel'>
            <div className="admin-panel-top">
                <SearchBar onSearch={handleSearch} />
                <button className='button new-button' onClick={handleAddServiceClick}>Dodaj konto</button>
            </div>
            {addingForm && (
                <form className='adding-employee-form' onSubmit={handleAddingFormSubmit}>
                    <div className='inputs'>
                        <div className='div-pair'>
                            <div className='label-input-pair'>
                                <label>Imie</label>
                                <input name='name' onChange={handleInputChange(setFormInputs)} value={formInputs.name} type="text" required />
                            </div>
                            <div className='label-input-pair'>
                                <label>Nazwisko</label>
                                <input name='surname' onChange={handleInputChange(setFormInputs)} value={formInputs.surname} type="text" required />
                            </div>
                        </div>
                        <div className='div-pair'>
                            <div className='label-input-pair'>
                                <label>Stanowisko</label>
                                <select name='position' onChange={handleInputChange(setFormInputs)} value={formInputs.position} required>
                                    <option value="" disabled>Select position</option>
                                    <option value="DR">DR</option>
                                    <option value="ADM">ADM</option>
                                    <option value="RE">RE</option>
                                </select>
                            </div>
                            <div className='label-input-pair'>
                                <label>E-mail</label>
                                <input name='email' onChange={handleInputChange(setFormInputs)} value={formInputs.email} type="text" required />
                            </div>
                        </div>
                    </div>
                    <button className='add-button' type='submit'>Zatwierd</button>
                </form>
            )}
            <Row
                array={filteredEmployees.length > 0 ? filteredEmployees : employees}
                idField="userId"
                properties={['name', 'surname', 'position', 'email']}
                handleEditConfirm={handleEditEmployeeData}
                handleInputChange={handleInputChange}
                serviceMod={empMod}
                setMod={setEmpMod}
                handleDeleteConfirm={handleDeleteEmployee}

            />
        </div>
    );
}

export default Personel;
