import React from "react";

function NavBar({ loginOpened, handleLogInClick,profileOpened,handleLinkClick }) { // Destructure props directly


    return (

        <div className='navbar'>
            {!loginOpened ? (
            <div className='navbar-left'>
                <div className='link' onClick={() => handleLinkClick('service')}>Uslugi</div>
                <div className='link' onClick={() => handleLinkClick('contact')}>Kontakt</div>
            </div>
            ) : (
                <div className='navbar-left'>
                    <div className='link' onClick={() => handleLinkClick('landing')}>Strona gwna</div>
                </div>
            )
            }
            <div className='navbar-right'>
                {!loginOpened && (
                    <button className='login'
                            onClick={handleLogInClick}>{!profileOpened ? "Sign In" : "Mj Profil"}</button>
                    )
                }
            </div>
        </div>
    );
}

export default NavBar;
