import NavBar from "./components/NavBar";
import Landing from "./components/Landing";
import Services from "./components/Services";
import Contact from "./components/Contact";
import LoginForm from "./components/LoginForm";
import { createContext, useEffect, useState } from "react";
import Alert from "./components/Alert";

export const DataContext = createContext({});

function getFromLocalStorage(key, defaultValue) {
    const value = localStorage.getItem(key);
    return value !== null ? JSON.parse(value) : defaultValue;
}

function App() {
    const [loginOpened, setLoginOpened] = useState(getFromLocalStorage('loginOpened', false));
    const [profileOpened, setProfileOpened] = useState(getFromLocalStorage('profileOpened', false));
    const [showNavbar, setShowNavbar] = useState(getFromLocalStorage('showNavbar', true));

    const [employees, setEmployees] = useState([]);
    const [services, setServices] = useState([]);
    const [visits, setVisits] = useState([]);
    const [clients, setClients] = useState([]);

    const [alertOpened, setAlertOpened] = useState(false);
    const [alertData, setAlertData] = useState({ message: "", color: "" });

    const [visitFormOpen, setVisitFormOpen] = useState(false)

    const fetchEmployees = () => {
        fetch("http://localhost:8090/api/employee/all")
            .then(response => response.json())
            .then(data => {
                setEmployees(data);
            })
            .catch(error => console.error("Error fetching data:", error));
    };

    useEffect(() => {
        fetchEmployees();
    }, []);

    const fetchServices = () => {
        fetch("http://localhost:8090/api/service/all")
            .then(response => response.json())
            .then(data => {
                setServices(data);
            })
            .catch(error => console.error("Error fetching data:", error));
    };

    useEffect(() => {
        fetchServices();
    }, []);

    const fetchVisits = () => {
        fetch("http://localhost:8090/api/visit/all")
            .then(response => response.json())
            .then(data => {
                // Sort the visits array by date and startTime ascending
                data.sort((a, b) => {
                    // Compare dates first
                    const dateComparison = new Date(a.date) - new Date(b.date);
                    if (dateComparison !== 0) {
                        return dateComparison;
                    }
                    // If dates are the same, compare start times
                    return a.startTime.localeCompare(b.startTime);
                });
                setVisits(data);
            })
            .catch(error => console.error("Error fetching data:", error));
    };

    useEffect(() => {
        fetchVisits();
    }, []);

    const fetchClients = () => {
        fetch("http://localhost:8090/api/client/all")
            .then(response => response.json())
            .then(data => {
                setClients(data);
            })
            .catch(error => console.error("Error fetching data:", error));
    };

    useEffect(() => {
        fetchClients();
    }, []);

    useEffect(() => {
        localStorage.setItem('loginOpened', JSON.stringify(loginOpened));
        localStorage.setItem('profileOpened', JSON.stringify(profileOpened));
        localStorage.setItem('showNavbar', JSON.stringify(showNavbar));
    }, [loginOpened, profileOpened, showNavbar]);

    const handleLinkClick = (to) => {
        const destination = document.getElementById(to);
        setLoginOpened(false); // Access setLoginOpened directly
        if (destination) {
            const topOffset = destination.offsetTop;
            window.scrollTo({ top: topOffset, behavior: 'smooth' });
        }
    };

    const handleBookClick = () => {
        if (profileOpened && JSON.parse(localStorage.getItem('userType')) === "client") {
            setLoginOpened(true);
            setVisitFormOpen(true);
            setShowNavbar(false);
        } else {
            handleLinkClick("contact");
        }
    };

    const contextValue = {
        employees,
        services,
        visits,
        clients,
        fetchEmployees,
        fetchServices,
        fetchVisits,
        setAlertOpened,
        setAlertData,
        alertData,
        alertOpened,
        setEmployees,
        setVisitFormOpen,
        visitFormOpen,
        setProfileOpened,
        setShowNavbar
    };

    const handleLoginClick = () => {
        setLoginOpened(true);

        profileOpened ? setShowNavbar(false) : setShowNavbar(true);
        profileOpened ? localStorage.setItem('showNavbar', 'false') : localStorage.setItem('showNavbar', 'true');
    };

    const handleCloseAlert = () => {
        setAlertOpened(false);
    };

    return (
        <DataContext.Provider value={contextValue}>
            <div className="App">
                {showNavbar && (
                    <NavBar setShowNavbar={setShowNavbar} loginOpened={loginOpened} handleLinkClick={handleLinkClick} handleLogInClick={handleLoginClick} setLoginOpened={setLoginOpened} profileOpened={profileOpened} />
                )}
                {!loginOpened && (
                    <>
                        <Landing handleBookClick={handleBookClick} />
                        <Services />
                        <Contact />
                    </>
                )}
                {loginOpened && (
                    <>
                        <LoginForm setShowNavbar={setShowNavbar} setLoginOpened={setLoginOpened} profileOpened={profileOpened} setProfileOpened={setProfileOpened} />
                    </>
                )}
                {alertOpened && (
                    <Alert
                        message={alertData.message}
                        color={alertData.color}
                        duration={3000}
                        onClose={handleCloseAlert} />
                )}
            </div>
        </DataContext.Provider>
    );
}

export default App;
