import React, {useContext, useState} from 'react';
import {DataContext} from "../App";

const VisitForm = ({setVisitFormOpen, deleteVisitAfterChangingTime}) => {
    const [formData, setFormData] = useState({
        date: '',
        time: '',
        employee: 0,
        service: 0,
    });

    const { employees, services, visits, fetchVisits, setAlertOpened, setAlertData } = useContext(DataContext);

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData({
            ...formData,
            [name]: value,
        });
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        // Handle form submission logic here
        console.log('Form data:', formData);

        fetch(`http://localhost:8090/api/visit/book?clientId=${JSON.parse(localStorage.getItem('userData')).userId}&serviceId=${formData.service}&visitId=${formData.time}`)
        .then(()=>{
                setVisitFormOpen(false);
                setAlertOpened(true);
                setAlertData({
                    message: 'Wizyta zostaa umwiona',
                    color: 'grey'
                });
                fetchVisits();
                setFormData({
                    date: '',
                    time: '',
                    employee: 0,
                    service: 0,
                })
            if (deleteVisitAfterChangingTime) {
                setTimeout(() => {
                    deleteVisitAfterChangingTime();
                }, 2000); // 2000 milliseconds = 2 seconds
            }
            }).catch(()=>{
            setAlertOpened(true);
            setAlertData({
                message: 'Wystpi bd podczas umawiania wizyty',
                color: 'darkred'
            });
    })

    };

    const checkVisitForDate = (id) =>{
        const visitsForEmployee = visits.filter((visit) => {
            const [year, month, day] = visit.date.split('-').map(Number);
            const formattedDate = `${year}-${month.toString().padStart(2, "0")}-${day.toString().padStart(2, "0")}`;
            return formattedDate === formData.date && visit.status === "FREE" &&
                 visit.employee.userId === id;
        })
        console.log(visitsForEmployee)
        return visitsForEmployee.length>0;
    }

    return (
        <form onSubmit={handleSubmit} className='visit-form'>
            <div className='visit-form-top'>
                <div className='name-pair'>
                    <div>
                        <label htmlFor="date">Data wizyty</label>
                        <input
                            type="date"
                            id="date"
                            name="date"
                            lang="pl"
                            value={formData.date}
                            onChange={handleChange}
                        />
                    </div>
                    <div>
                        <label htmlFor="time">Termin wizyty</label>
                        <select name="time" id="time" value={formData.time} onChange={handleChange} required>
                            <option value="">Wybierz termin</option>
                            {visits.filter((visit) => {
                                const [year, month, day] = visit.date.split('-').map(Number);
                                const formattedDate = `${year}-${month.toString().padStart(2, "0")}-${day.toString().padStart(2, "0")}`;
                                return (formData.date ? formattedDate===formData.date : true) && visit.status === "FREE" &&
                                    (formData.employee ? visit.employee.userId.toString()===formData.employee : true );
                            }).length === 0 ? (
                                <option value="">Brak dostpnych terminw</option>
                            ) : (
                                visits.filter((visit) => (formData.date ? visit.date === formData.date : true) && visit.status === "FREE" &&
                                    (formData.employee ? visit.employee.userId.toString()===formData.employee : true ))
                                    .map((visit, index) => (
                                        <option key={index} value={visit.visitId}
                                                aria-label={` wizyta od ${visit.startTime} do ${visit.endTime}`}>
                                            {formData.date ? "" : visit.date} {visit.startTime.slice(0, 5)} - {visit.endTime.slice(0, 5)} {formData.employee ? "" : `(${visit.employee.name} ${visit.employee.surname})`}
                                        </option>
                                    ))
                            )}
                        </select>
                    </div>
                </div>
                <div className='name-pair'>
                    <div>
                        <label>Rehabilitant</label>
                        <select name="employee" value={formData.employee} onChange={handleChange}>
                            <option value="">Wybierz rehabilitanta</option>
                            {employees.filter(employee => {
                                return employee.position === 'DR' && (formData.date ? checkVisitForDate(employee.userId) : true)
                            })
                                .map((employee, index) => (
                                    <option key={index}
                                            value={employee.userId}>{employee.name} {employee.surname}</option>
                                ))}
                        </select>
                    </div>
                    <div>
                        <label>Zabieg</label>
                        <select name="service" value={formData.service} onChange={handleChange} required>
                            <option value="">Wybierz zabieg</option>
                            {services.map((service, index) => (
                            <option key={index} value={service.serviceId}>{service.name}</option>
                        ))}
                    </select>
                </div>
                </div>
                {/*    <div className='name-pair'>*/}
                {/*        <div>*/}
                {/*            <label>Imi</label>*/}
                {/*            <input type="text" name="name" value={formData.firstName} onChange={handleChange} required/>*/}
                {/*        </div>*/}
                {/*        <div>*/}
                {/*            <label>Nazwisko</label>*/}
                {/*            <input type="text" name="surname" value={formData.lastName} onChange={handleChange} required/>*/}
                {/*        </div>*/}
            </div>
            {/*</div>*/}
            {/*<label>Numer telefonu</label>*/}
            {/*<input type="tel" name="phoneNumber" value={formData.phoneNumber} onChange={handleChange} required />*/}

            <button className='add-button' type="submit">Umw</button>
        </form>
    );
};

export default VisitForm;