import React, {useContext, useEffect, useState} from 'react';
import Personel from "./Personel";
import AdminServiceMod from "./AdminServiceMod";
import {faArrowRightFromBracket} from "@fortawesome/free-solid-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import EmpCalendar from "./EmpCalendar";
import ArchiwumWizyt from "./ArchiwumWizyt";
import ClientPersonalData from "./ClientPersonalData";
import VisitsClient from "./VisitsClient";
import {DataContext} from "../App";
import HarmonogramGabinetu from "./HarmonogramGabinetu";
import VisitForm from "./VisitForm";


const LoginForm = ({setShowNavbar, setLoginOpened,profileOpened,setProfileOpened}) => {

    const {setAlertOpened, setAlertData, clients } = useContext(DataContext);

    // State variables to store login data
    const [loginData, setLoginData] = useState({
        login: '',
        password: ''
    });

    const [userData, setUserData] = useState('');
    const [userType, setUserType] = useState('');
    const [options, setOptions] = useState([]);
    const [activeOption, setActiveOption] = useState('');

    useEffect(() => {
        // Retrieve data from local storage on component mount
        const storedUserData = localStorage.getItem('userData');
        const storedUserType = localStorage.getItem('userType');
        const storedOptions = localStorage.getItem('options');
        const storedActiveOption = localStorage.getItem('activeOption');

        // Parse JSON data from local storage and update state
        if (storedUserData) setUserData(JSON.parse(storedUserData));
        if (storedUserType) setUserType(JSON.parse(storedUserType));
        if (storedOptions) setOptions(JSON.parse(storedOptions));
        if (storedActiveOption) setActiveOption(JSON.parse(storedActiveOption));
    }, []);

    // State variables to store register data
    const [registerData, setRegisterData] = useState({
        name: '',
        surName: '',
        mail: '',
        phone: '',
        pass: '',
        passRepeat: '',
        pesel: '',
    });

    //bannerToggling
    const [shift, setShiftToLeft] = useState(false);

    //password form state
    const[forgetPassword, setForgetPassword] = useState(false);
    let[mailReset, setMailReset] = useState('');

    const optionsAdmin=['Usugi i zabiegi', 'Zarzdzanie personelem'];
    const optionsClient=['Nadchodzce wizyty', 'Historia wizyt', 'Dane osobowe'];
    const optionsDr=['Harmonogram', 'Archiwum wizyt'];
    const optionsRecep=['Harmonogram gabinetu'];


    const handleSubmit = (e) => {
        e.preventDefault();


        fetch(`http://localhost:8090/api/client/email?email=${loginData.login}`)
            .then(response => response.json())
            .then(data => {
                if (!data) {
                    fetch(`http://localhost:8090/api/employee/email?email=${loginData.login}`)
                        .then(response => response.json())
                        .then(data => {
                            if (!data) {
                                setAlertOpened(true);
                                setAlertData({
                                    message: "Uytkownik o podanym adresie email nie istnieje.",
                                    color: "#bd4660"
                                })
                            } else {
                                // Set the retrieved data to the employees state
                                console.log(data);
                                fetch(`http://localhost:8090/api/employee/authentication?email=${loginData.login}&password=${loginData.password}`)
                                    .then(response => response.json())
                                    .then(data => {
                                        if (data.position!==undefined) {
                                            // Save each piece of data separately in local storage
                                            localStorage.setItem('profileOpened', JSON.stringify(true));
                                            setShowNavbar(false);
                                            localStorage.setItem('showNavbar', 'false')
                                            localStorage.setItem('userData', JSON.stringify(data));
                                            localStorage.setItem('userType', JSON.stringify(data.position));

                                            switch (data.position) {
                                                case 'ADM':
                                                    localStorage.setItem('options', JSON.stringify(optionsAdmin));
                                                    localStorage.setItem('activeOption', JSON.stringify(optionsAdmin[0]));
                                                    setOptions(optionsAdmin);
                                                    setActiveOption(optionsAdmin[0]);
                                                    break;
                                                case 'DR':
                                                    localStorage.setItem('options', JSON.stringify(optionsDr));
                                                    localStorage.setItem('activeOption', JSON.stringify(optionsDr[0]));
                                                    setOptions(optionsDr);
                                                    setActiveOption(optionsDr[0]);
                                                    break;
                                                case 'RE':
                                                    localStorage.setItem('options', JSON.stringify(optionsRecep));
                                                    localStorage.setItem('activeOption', JSON.stringify(optionsRecep[0]));
                                                    setOptions(optionsRecep);
                                                    setActiveOption(optionsRecep[0]);
                                                    break;
                                                default:
                                                    break;
                                            }

                                            setUserType(data.position);
                                            setProfileOpened(true);
                                            setUserData(data);
                                        } else {
                                            setAlertOpened(true);
                                            setAlertData({
                                                message: "Niepoprawne haso.",
                                                color: "#bd4660"
                                            })
                                        }
                                    })
                                    .catch(error => console.error("Error fetching employee data:", error));
                            }
                        })
                } else {
                    console.log(data);
                    fetch(`http://localhost:8090/api/client/authentication?email=${loginData.login}&password=${loginData.password}`)
                        .then(response => response.json())
                        .then(data => {
                            if (data) {
                                // Save each piece of data separately in local storage
                                localStorage.setItem('profileOpened', JSON.stringify(true));
                                setShowNavbar(false);localStorage.setItem('showNavbar', 'false')
                                localStorage.setItem('userData', JSON.stringify(data));
                                localStorage.setItem('userType', JSON.stringify('client'));
                                localStorage.setItem('options', JSON.stringify(optionsClient));
                                localStorage.setItem('activeOption', JSON.stringify(optionsClient[0]));
                                setProfileOpened(true);
                                setUserData(data);
                                setUserType('client');
                                setOptions(optionsClient);
                                setActiveOption(optionsClient[0]);
                            } else {
                                setAlertOpened(true);
                                setAlertData({
                                    message: "Niepoprawne haso.",
                                    color: "#bd4660"
                                })
                            }
                        })
                        .catch(error => console.error("Error fetching employee data:", error));
                }
            })
            .catch(error => console.error("Error fetching client data:", error));

        setLoginData({
            ...loginData,
            password: ''
        });
    };

    const handleInputChange = (setState) => (e) => {
        const { name, value } = e.target;
        setState((prevState) => ({
            ...prevState,
            [name]: value,
        }));
    };

    const handleRegisterSubmit = (e) => {
        e.preventDefault();

        const { name, surName, mail, phone, pass, passRepeat, pesel } = registerData;
        console.log(registerData);
        if (pass === passRepeat) {
            fetch(`http://localhost:8090/api/client/add?name=${name}&surname=${surName}&password=${pass}&email=${mail}&pesel=${pesel}&phoneNr=${phone}`)
                .then(() => {
                    // Client added successfully
                    console.log("Client added successfully");
                    setShiftToLeft(!shift);
                    // Reset the form fields
                    setRegisterData({
                        name: '',
                        surName: '',
                        mail: '',
                        phone: '',
                        pass: '',
                        passRepeat: '',
                        pesel: '',
                    });
                })
                .catch(error => {
                    console.log("Error adding new client to db");
                });
        } else {
            setRegisterData({
                ...registerData,
                pass: '',
                passRepeat: '',
            });
            setAlertOpened(true);
            setAlertData({
                message: "Wprowadzone hasa si nie zgadzaj.",
                color: "#bd4660"
            })
        }
    };

    const [codeVer, setCodeVer] = useState('');
    const [codeSent, setCodeSent] = useState(false);
    const [passwordChangeInputOpened, setPasswordChangeInputOpened] = useState(false);

    const handlePasswordSubmit = (e) => {
        e.preventDefault();

        let exist = false;
        clients.forEach(client =>{
            if(client.email===mailReset) exist=true;
        })

        if(exist){
        if (!codeSent) {
            fetch(`http://localhost:8090/api/reset/password?email=${mailReset}`)
                .then(() => {
                    console.log("Code sent succesfully")
                    setCodeVer('');
                    setCodeSent(true);
                })
                .catch(error => {
                    console.log("Error sending code");
                });
        } else {
            fetch(`http://localhost:8090/api/reset/code?email=${mailReset}`)
                .then(response => response.json())
                .then(response => {
                    console.log(response);
                    console.log(codeVer);
                    if (response.verificationCode === codeVer) {
                        setPasswordChangeInputOpened(true);
                        setCodeSent(false);
                        //usunac z bazy ten kod
                    } else {
                        setAlertOpened(true);
                        setAlertData({
                            message: "Niepoprawny kod weryfilaccyjny!",
                            color: "#bd4660"
                        })
                    }
                })
                .catch(error => {
                    console.log("Error reseting password");
                });
        }
    }else{
            setAlertOpened(true);
            setAlertData({
                message: "Uytkownik o podanym adresie e-mail nie istnieje",
                color: "darkred"
            })
        }
    }

    const renderOptionComponent = () => {
        switch (userType) {
            case 'client':
                switch (activeOption) {
                    case 'Nadchodzce wizyty':
                        return <VisitsClient time={"future"}/>;
                    case 'Historia wizyt':
                        return <VisitsClient time={"past"}/>;
                    case 'Dane osobowe':
                        return <ClientPersonalData />;
                    default:
                        return null;
                }
            case 'ADM':
                switch (activeOption) {
                    case 'Usugi i zabiegi':
                        return <AdminServiceMod />;
                    case 'Zarzdzanie personelem':
                        return <Personel />;
                    default:
                        return null;
                }
            case 'DR':
                // Second switch to handle options for Type1
                switch (activeOption) {
                    case 'Harmonogram':
                        return <EmpCalendar id={userData.userId} />;
                    case 'Archiwum wizyt':
                        return <ArchiwumWizyt />;
                    default:
                        return null;
                }
            case 'RE':
                // Second switch to handle options for Type2
                switch (activeOption) {
                    case 'Harmonogram gabinetu':
                        return <HarmonogramGabinetu />;
                    default:
                        return null;
                }
            default:
                return null;
        }
    };

    const logout = () => {
        setProfileOpened(false);
        setShowNavbar(true);
        localStorage.setItem('showNavbar', 'true');
        // Set data in local storage to empty values
        localStorage.setItem('userData', JSON.stringify(''));
        localStorage.setItem('userType', JSON.stringify(''));
        localStorage.setItem('options', JSON.stringify([]));
        localStorage.setItem('activeOption', JSON.stringify(''));

        setUserData('');
        setUserType('');
        setOptions([]);
        setActiveOption('');
        setLoginData({
            ...loginData,
            login: ''
        });
    };

    const handleResetLinkClick = () =>{
        setForgetPassword(!forgetPassword);
        setCodeSent(false);
    }

    const [newPassword, setNewPassword] = useState({
        pass: '',
        passRepeat: ''
    });
    const handlePasswordChange = (e) =>{
        e.preventDefault()
        if(newPassword.pass===newPassword.passRepeat){
            console.log("hasla sa takie same")
            fetch(`http://localhost:8090/api/client/changePassword?email=${mailReset}&password=${newPassword.pass}`)
                .then(()=>{
                    setAlertOpened(true);
                    setAlertData({
                        message: "Haso zostao zmienione",
                        color: "grey"
                    });
                })
                .catch(error => {
                    console.log(error);
                })
            setPasswordChangeInputOpened(false);
            setForgetPassword(false);
            setNewPassword({
                pass:'',
                passRepeat: ''
            });
        }else{
            setAlertOpened(true);
            setAlertData({
                message: "Wprowadzone hasa si nie zgadzaj!",
                color: "#db5673"
            })
        }
    }


    return (
        <>
            {!profileOpened ?  (<div className='login-container' id='landing'>
                <div className='login-form'>
                    <div className='login-left'>
                        <div className='login-form-header'>Zaloguj si</div>
                        <div className='login-form-subheader'>Jeeli masz ju konto w naszym systemie, zaloguj si uywajc
                            nazwy i hasa uytkownika
                        </div>
                        {forgetPassword ? (
                            <>
                            {!passwordChangeInputOpened ? (
                            <form onSubmit={handlePasswordSubmit}>
                                {!codeSent ? (
                                        <>
                                            <label>E-mail</label>
                                            <input
                                                type="text"
                                                value={mailReset}
                                                onChange={(e) => setMailReset(e.target.value)}
                                                required
                                                placeholder='e-mail'
                                            />
                                        </>
                                    ) :
                                    (
                                        <>
                                            <label>Kod weryfikacyjny</label>
                                            <input
                                                type="text"
                                                value={codeVer}
                                                onChange={(e) => setCodeVer(e.target.value)}
                                                required
                                                placeholder='kod weryfikacji'
                                            />
                                        </>
                                    )}
                                <button type="submit">{codeSent ? "Zresetuj haso" : "Wylij kod weryfikacji"}</button>
                            </form>
                        ) : (
                            <form onSubmit={handlePasswordChange}>

                                        <>
                                            <label>Nowe haso</label>
                                            <input
                                                type="password"
                                                value={newPassword.pass}
                                                name="pass"
                                                onChange={handleInputChange(setNewPassword)}
                                                required
                                                placeholder=''
                                            />
                                        </>
                                        <>
                                            <label>Powtrz haso</label>
                                            <input
                                                type="password"
                                                name="passRepeat"
                                                value={newPassword.passRepeat}
                                                onChange={handleInputChange(setNewPassword)}
                                                required
                                                placeholder=''
                                            />
                                        </>
                                <button type="submit">Zmie haso</button>
                            </form>
                        )}
                            </>
                        ) : (
                            <form onSubmit={handleSubmit}>
                        <div>
                            <label>Nazwa uytkownika</label>
                            <input
                                type="text"
                                name="login"
                                value={loginData.login}
                                onChange={handleInputChange(setLoginData)}
                                required
                                placeholder='login'
                            />
                        </div>
                        <div>
                            <label>Haso</label>
                            <input
                                type="password"
                                name="password"
                                value={loginData.password}
                                onChange={handleInputChange(setLoginData)}
                                required
                                placeholder='password'
                            />
                        </div>
                        <button type="submit">Log in</button>
                    </form>
                    )}
                    <div className='login-form-bottom-links top-border'
                         onClick={handleResetLinkClick}>{!forgetPassword ? "Zapomniaem hasa" : "Wr do logowania"}</div>
                    <div className='login-form-bottom-links' onClick={() => setShiftToLeft(!shift)}>Chc zarejestrowa si w systemie
                        </div>
                    </div>
                    <div className='login-left right'>
                        <form onSubmit={handleRegisterSubmit}>
                            <div className='login-form-header'>Za konto</div>
                            <div className='login-form-subheader'>
                                Zarejestruj sie w naszym systemie i korzystaj z dodatkowych funckji
                            </div>
                            <div className='name-input'>
                                <div>
                                    <label>Imi</label>
                                    <input
                                        type="text"
                                        name="name"
                                        value={registerData.name}
                                        onChange={handleInputChange(setRegisterData)}
                                    />
                                </div>
                                <div>
                                    <label>Nazwisko</label>
                                    <input
                                        type="text"
                                        name="surName"
                                        value={registerData.surName}
                                        onChange={handleInputChange(setRegisterData)}
                                    />
                                </div>
                            </div>
                            <div>
                                <label>E-mail</label>
                                <input
                                    type="email"
                                    name="mail"
                                    value={registerData.mail}
                                    onChange={handleInputChange(setRegisterData)}
                                />
                            </div>
                            <div className='name-input'>
                                <div>
                                <label>Numer telefonu</label>
                                    <input
                                        type="tel"
                                        name="phone"
                                        value={registerData.phone}
                                        onChange={handleInputChange(setRegisterData)}
                                        required
                                        placeholder='xxx-xxx-xxx'
                                    />
                                </div>
                                <div>
                                    <label>PESEL</label>
                                    <input
                                        type="text"
                                        name="pesel"
                                        value={registerData.pesel}
                                        onChange={handleInputChange(setRegisterData)}
                                        required
                                        placeholder='pesel'
                                    />
                                </div>
                            </div>
                            <div className='name-input'>
                                <div>
                                    <label>Haso</label>
                                    <input
                                        type="password"
                                        name="pass"
                                        value={registerData.pass}
                                        onChange={handleInputChange(setRegisterData)}
                                        required
                                        placeholder='password'
                                    />
                                </div>
                                <div>
                                    <label>Powtrz haso</label>
                                    <input
                                        type="password"
                                        name="passRepeat"
                                        value={registerData.passRepeat}
                                        onChange={handleInputChange(setRegisterData)}
                                        required
                                        placeholder='password'
                                    />
                                </div>
                            </div>
                            <button type="submit">Za konto</button>
                            <div className='login-form-bottom-links top-border center' onClick={()=>setShiftToLeft(!shift)}>Mam ju konto</div>
                        </form>
                    </div>
                    <div className={`${!shift ? 'shift' : 'shift shift-right'}`}></div>
                </div>
            </div>) : (
                <>
                    <div className='navbar'>
                        <div className="main-page-link" onClick={() => {setLoginOpened(false); setShowNavbar(true); localStorage.setItem('showNavbar', 'true')}}>Strona gwna</div>
                        <div className='data'>
                            <div>{userData.name}</div>
                            <div>{userData.surname}</div>
                            <button className='logout-button' onClick={logout}><FontAwesomeIcon
                                icon={faArrowRightFromBracket}/>
                            </button>
                        </div>

                    </div>
                    <div className='profile'>
                        <div className='profile-menu'>
                            <div className='profile-nav'>
                                {options.map((option, index) => (
                                    <div
                                        key={index} // Added key prop for optimization
                                        className={option !== activeOption ? 'profile-nav-option' : 'profile-nav-option option-active'}
                                        onClick={() => {
                                            setActiveOption(option);
                                            localStorage.setItem('activeOption', JSON.stringify(option));
                                        }} // Pass index to setActiveOption
                                    >
                                        {option}
                                    </div>
                                ))}
                            </div>
                            <div className='opened-option'>{renderOptionComponent()}</div>
                        </div>
                    </div>
                </>
            )}
        </>

    );
};

export default LoginForm;