import React, {useContext, useEffect, useState} from "react";
import {DataContext} from "../App";
import VisitForm from "./VisitForm";


function VisitsClient({time}){
    const { visits, fetchVisits, setVisitFormOpen, visitFormOpen } = useContext(DataContext);
    const [pastVisits, setPastVisits] = useState([]);
    const [visitOpened, setVisitOpened] = useState(null);
    const [visitCancel, setVisitCancel] = useState(null);
    const [visitChange, setVisitChange] = useState(null)

    useEffect(() => {
        if(time==='past'){
            visits.sort((a, b) => {
                // Compare dates first
                const dateComparison = new Date(b.date) - new Date(a.date);
                if (dateComparison !== 0) {
                    return dateComparison;
                }
                // If dates are the same, compare start times
                return b.startTime.localeCompare(a.startTime);
            });
        }else{
            visits.sort((a, b) => {
                // Compare dates first
                const dateComparison = new Date(a.date) - new Date(b.date);
                if (dateComparison !== 0) {
                    return dateComparison;
                }
                // If dates are the same, compare start times
                return a.startTime.localeCompare(b.startTime);
            });
        }

        const pastVisits = visits
            .filter((visit) => {
            const dbDateTimeString = `${visit.date}T${visit.startTime}`; // e.g., "2024-05-19T14:30:00"
            const dbDateTime = new Date(dbDateTimeString);
            const currentDateTime = new Date();

            switch (time){
                case "past" :
                    return dbDateTime <= currentDateTime &&  visit.status === "BOOKED" && visit.client && visit.client.userId===JSON.parse(localStorage.getItem('userData')).userId;
                case "future":
                    return dbDateTime > currentDateTime && visit.status === "BOOKED" && visit.client && visit.client.userId===JSON.parse(localStorage.getItem('userData')).userId;
                default:
                    break;
            }
        });

        setPastVisits(pastVisits)
    }, [visits, time]);


    const [searchQuery, setSearchQuery] = useState('');

    const handleSearch = (event) => {
        setSearchQuery(event.target.value.toLowerCase());
    };

    const highlightMatch = (text) => {
        if (!searchQuery) return text;

        const regex = new RegExp(`(${searchQuery})`, 'gi');
        const parts = text.split(regex);

        return parts.map((part, index) =>
            part.toLowerCase() === searchQuery ? <span key={index} className="highlight">{part}</span> : part
        );
    };

    const filteredVisits = pastVisits.filter(visit => {
        const dateMatch = visit.date.toLowerCase().includes(searchQuery);
        const startTimeMatch = visit.startTime.toLowerCase().includes(searchQuery);
        const serviceMatch = visit.service.name.toLowerCase().includes(searchQuery);
        const employeeMatch = `${visit.employee.name} ${visit.employee.surname}`.toLowerCase().includes(searchQuery);
        const resultMatch = visit.result ? visit.result.toLowerCase().includes(searchQuery) : "";

        return dateMatch || startTimeMatch || serviceMatch || employeeMatch || resultMatch;
    });

    const handleAddVisitClick = () =>{
        setVisitFormOpen(!visitFormOpen);
    }

    const handleVisitClick = (visit) =>{
        visitOpened ? setVisitOpened(null) : setVisitOpened(visit);
    }

    const handleDeleteClick = (e,visit)=>{
        e.stopPropagation();
        setVisitCancel(visit);
        setVisitChange(null);
    }

    const confirmDelete = () => {
        console.log("visit to be deleted"+ visitCancel)
        fetch(`http://localhost:8090/api/visit/cancel-visit?visitId=${visitCancel.visitId}`)
            .then(()=>{
                setVisitOpened(null);
                setVisitCancel(null);
                fetchVisits();
            })
            .catch()
    }

    const handleAnulujClick = (e)=>{
        e.stopPropagation();
        setVisitCancel(null)
    }

    const changeTime = (e,visit) =>{
        e.stopPropagation();
        visitChange ? setVisitChange(null) : setVisitChange(visit);
        setVisitCancel(null)
    };

    const deleteVisitAfterChangingTime = () =>{
        console.log("wizyta zostanie usunieta"+visitChange)
        fetch(`http://localhost:8090/api/visit/cancel-visit?visitId=${visitChange.visitId}`)
            .then(()=>{
                setVisitChange(null);
                setVisitOpened(null)
                fetchVisits();
            })
            .catch()
    }

    return (
        <div className='admin-panel'>
            {time==='future' && (
                <div className="new-visit-div">
                    <button className='button new-button' onClick={handleAddVisitClick}>Umw wizyt</button>
                </div>
            )}
            {visitFormOpen && time==='future' && (
                <VisitForm setVisitFormOpen={setVisitFormOpen}/>
            )
            }
            {time === 'past' && (
                <input type='text' name='search' className='archiwum-input' onChange={handleSearch}
                       placeholder='Szukaj w archiwum...'/>
            )}
            {filteredVisits.length>0 ? filteredVisits.map((visit, index) => (
                    <div key={index} className='row'>
                        <div className='row-up' onClick={()=>handleVisitClick(visit)}>
                            <div>{highlightMatch(visit.date)}</div>
                            <div>{highlightMatch(visit.startTime)}</div>
                            <div>{highlightMatch(visit.service.name)}</div>
                            <div>{highlightMatch(`${visit.employee.name} ${visit.employee.surname}`)}</div>
                        </div>
                        {time === 'past' && (
                            <div className='visit-description'>{visit.result && highlightMatch(visit.result)}</div>
                        )}
                        {time==='future' && visitOpened===visit && (
                            <>
                                <div className='down'>
                                    <div onClick={(e) => changeTime(e,visit)}>Zmie termin</div>
                                    <div className='row-menu' onClick={(e) => handleDeleteClick(e,visit)}>Odwoaj</div>
                                </div>
                                {visitChange===visit && (
                                    <>
                                        <div>Po umwieniu nowej wizyty poprzednia zostanie odwoana!</div>
                                        <VisitForm deleteVisitAfterChangingTime={deleteVisitAfterChangingTime}  setVisitFormOpen={setVisitFormOpen}/>
                                    </>
                                )}
                                {visitCancel===visit && (
                                    <div className='confirm-div width-80'>
                                        <div> Czy na pewno chcesz odwoa t wizyt?</div>
                                        <div className='width-30'>
                                            <button className='add-button red '
                                                    onClick={(e)=>handleAnulujClick(e)}>Anuluj
                                            </button>
                                            <button className='add-button white'
                                                    onClick={confirmDelete}>Tak
                                            </button>
                                        </div>
                                    </div>
                                )}
                            </>
                        )}
                    </div>
            )) : (
                <div className='placeholder'>Nie jeste umwiony na adn wizyt.</div>
            )}
        </div>
    )
}

export default VisitsClient