import React, {useContext, useState} from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faUser, faPhone, faEnvelope } from '@fortawesome/free-solid-svg-icons';
import {DataContext} from "../App";

function Contact(){

    const {setAlertOpened,setAlertData} = useContext(DataContext)

    const [contactForm, setContactForm] = useState({
        name: '',
        phone: '',
        email: '',
        message: ''
    });

    const handleChange = (e) => {
        const { name, value } = e.target;
        setContactForm(prevState => ({
            ...prevState,
            [name]: value
        }));
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        // Here you can do something with the contactForm state, such as sending it to a backend server
        console.log(contactForm);

        fetch(`http://localhost:8090/api/reset/form?email=${contactForm.email}&imie=${encodeURIComponent(contactForm.name)}&phone=${contactForm.phone}&text=${encodeURIComponent(contactForm.message)}`)
            .then(() => {

                setContactForm({
                    name: '',
                    phone: '',
                    email: '',
                    message: ''
                })

                setAlertOpened(true);
                setAlertData({
                    message: "Twoja wiadomo zostaa wysana",
                    color: "grey"
                })
            })
            .catch(error => {
                // Handle error here
                console.error('There was a problem with your fetch operation:', error);
            });
    };

    return (
        <div className='contact' id='contact'>
            <svg className='svg-done' viewBox="0 0 500 90">
                <path d="M 0,40 C 130,80 250,0 500,0 L 500,00 L 0,0"></path>
            </svg>
            <div className='contact-container'>
                <div className='contact-header'>Jak umwisz sie na wizyte?</div>
                <div className='contact-left'>
                    <div className='contact-left-section'>
                        <div className='icon'><FontAwesomeIcon icon={faUser}/></div>
                        <div className='icon-info'>Zaloguj si na konto lub za konto w systemie</div>
                    </div>
                    <div className='contact-left-section'>
                        <div className='icon'><FontAwesomeIcon icon={faPhone}/></div>
                        <div className='icon-info'>
                            <div>(48) 000-000-000</div>
                            <div>(48) 000-000-000</div>
                        </div>
                    </div>
                    <div className='contact-left-section'>
                        <div className='icon'><FontAwesomeIcon icon={faEnvelope}/></div>
                        <div className='icon-info'>Wylij do nas wiadomo przez formularz kontaktowy</div>
                    </div>

                </div>
                <div className='contact-right'>
                    <div>Formularz kontaktowy
                </div>
                <label htmlFor='name'>Dane osobowe</label>
                <input
                    name='name'
                    type="text"
                    placeholder='Imi'
                    value={contactForm.name}
                    onChange={handleChange}
                />
                <input
                    name='phone'
                    type='tel'
                    placeholder='Numer telefonu'
                    value={contactForm.phone}
                    onChange={handleChange}
                />
                <input
                    name='email'
                    type="email"
                    placeholder='E-mail'
                    value={contactForm.email}
                    onChange={handleChange}
                />
                <label htmlFor='message'>Wiadomo dla personelu</label>
                <textarea
                    name='message'
                    placeholder='Tekst wiadomoci'
                    value={contactForm.message}
                    onChange={handleChange}
                />
                <button onClick={handleSubmit}>Wylij</button>
            </div>
        </div>
</div>
)
}

export default Contact;