import React, { useState, useContext } from 'react';
import EmpCalendar from "./EmpCalendar";
import { DataContext } from "../App";

function HarmonogramGabinetu() {
    const { employees, services } = useContext(DataContext); // Assuming you have doctors and procedures in your context
    const [employee, setEmployee] = useState(0);
    const [service, setService] = useState(0);
    const [showOccupied, setShowOccupied] = useState(false);
    const [showFree, setShowFree] = useState(false);


    const handleDoctorChange = (e) => {
        setEmployee(parseInt(e.target.value));
        console.log(e.target.value)
    };
    const handleProcedureChange = (e) => setService(parseInt(e.target.value));
    const handleShowOccupiedChange = (e) => setShowOccupied(e.target.checked);
    const handleShowFreeChange = (e) => setShowFree(e.target.checked);

    const handleCleanFilters = () =>{
        setEmployee(0)
        setService(0)
        setShowOccupied(false)
        setShowFree(false)
    }

    return (
        <div>
            <div className='filters'>
                <div className='filters-select'>
                        <select value={employee} onChange={handleDoctorChange}>
                            <option value="">Wybierz lekarza</option>
                            {employees
                                .filter(employee =>{
                                    return employee.position==='DR';
                                })
                                .map((doc) => (
                                <option key={doc.userId} value={doc.userId}>{doc.name} {doc.surname}</option>
                            ))}
                        </select>
                </div>
                <div className='filters-select'>
                        <select value={service} onChange={handleProcedureChange}>
                            <option value="">Wybierz zabieg</option>
                            {services.map((proc) => (
                                <option key={proc.serviceId} value={proc.serviceId}>{proc.name}</option>
                            ))}
                        </select>
                </div>
                <div className='filters-checkbox'>
                    <label>
                        <input type="checkbox" checked={showOccupied} onChange={handleShowOccupiedChange}/>
                        Rezerwacje
                    </label>
                </div>
                <div className='filters-checkbox'>
                    <label>
                        <input type="checkbox" checked={showFree} onChange={handleShowFreeChange}/>
                        Wolne terminy
                    </label>
                </div>
                <div onClick={handleCleanFilters}>
                   Wyczy fltry
                </div>
            </div>
            <EmpCalendar
                id='-1'
                doctorFilter={employee}
                procedureFilter={service}
                showOccupiedFilter={showOccupied}
                showFreeFilter={showFree}
            />
        </div>
    );
}

export default HarmonogramGabinetu;
