import React, { useContext, useEffect, useState } from "react";
import { Calendar, momentLocalizer } from "react-big-calendar";
import moment from "moment";
import withDragAndDrop from "react-big-calendar/lib/addons/dragAndDrop";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes } from "@fortawesome/free-solid-svg-icons";
import "moment/locale/pl";
import "react-big-calendar/lib/addons/dragAndDrop/styles.css";
import "react-big-calendar/lib/css/react-big-calendar.css";
import { DataContext } from "../App";

const localizer = momentLocalizer(moment);
const DnDCalendar = withDragAndDrop(Calendar);

const EmpCalendar = ({ id, doctorFilter, procedureFilter, showOccupiedFilter, showFreeFilter }) => {
    const { services, visits, setAlertOpened, setAlertData,fetchVisits } = useContext(DataContext);
    const [events, setEvents] = useState([]);
    const [eventClicked, setEventClicked] = useState(null);
    const [visitDescription, setVisitDescription] = useState('');
    const [eventBook, setEventBook]= useState(null)
    const [formData, setFormData] = useState({
        name: '',
        surname: '',
        phone: '',
        pesel: '',
        service: 0,
    });

    const [client, setClient] = useState({})
    const [service, setService] = useState({})

    const [eventCancel, setEventCancel] = useState({})


    useEffect(() => {
        const currentDateTime = new Date();
        let filteredVisits = visits;

        if (doctorFilter) {
            console.log(visits)
            filteredVisits = filteredVisits.filter(visit => visit.employee.userId === doctorFilter);
            console.log(filteredVisits)
        }
        if (procedureFilter) {
            filteredVisits = filteredVisits.filter(visit => visit.service && visit.service.serviceId === procedureFilter);
        }
        if (showOccupiedFilter && !showFreeFilter) {
            filteredVisits = filteredVisits.filter(visit => visit.client);
        } else if (!showOccupiedFilter && showFreeFilter) {
            filteredVisits = filteredVisits.filter(visit => !visit.client);
        }

        if (id !== '-1') {
            filteredVisits = filteredVisits.filter(visit => visit.employee.userId === id);
        }


        let formattedEvents = filteredVisits
            .filter(visit => {
                const [year, month, day] = visit.date.split('-').map(Number);
                const [startHour, startMinute] = visit.startTime.split(':').map(Number);
                const startDate = new Date(year, month - 1, day, startHour, startMinute);
                return startDate >= currentDateTime || visit.client;
            })
            .map(visit => {
                const [year, month, day] = visit.date.split('-').map(Number);
                const [startHour, startMinute] = visit.startTime.split(':').map(Number);
                const [endHour, endMinute] = visit.endTime.split(':').map(Number);
                const startDate = new Date(year, month - 1, day, startHour, startMinute);
                const endDate = new Date(year, month - 1, day, endHour, endMinute);
                return {
                    start: startDate,
                    end: endDate,
                    title: visit.service && visit.service.name ? visit.service.name : "Wolny termin",
                    id: visit.visitId,
                    description: visit.result,
                    employee: visit.employee,
                    client: visit.client ? visit.client : ""
                };
            });

        setEvents(formattedEvents);
    }, [visits, id, doctorFilter, procedureFilter, showOccupiedFilter, showFreeFilter]);

    const onEventResize = (data) => {
        const { start, end } = data;
        setEvents([{ ...events[0], start, end }]);
    };

    const onEventDrop = (data) => {
        console.log(data);
    };

    const handleEventClick = (event) => {
        setEventClicked(event);
    };

    const closeOverlay = () => {
        setEventClicked(null);
    };

    const eventPropGetter = (event) => {
        const style = {
            backgroundColor: event.title === "Wolny termin" ? '#a87859' : '#3a7887',
        };
        return { style };
    };

    const handleTextareaChange = (event) => {
        setVisitDescription(event.target.value);
    };

    const handleSubmit = () => {
        fetch(`http://localhost:8090/api/visit/addDescription?id=${eventClicked.id}&result=${visitDescription}`)
            .then(() => {
                console.log("Data edited successfully");
            })
            .catch(error => {
                console.error('Error editing description data:', error);
            });
    };

    const handleBookClick = async () => {
        if (!eventBook) {
            setEventBook(eventClicked);
        } else {
            console.log(formData);
            console.log(eventBook);

            try {
                // Add new client
                await fetch(`http://localhost:8090/api/client/add-no-account?name=${encodeURIComponent(formData.name)}&surname=${encodeURIComponent(formData.surname)}&pesel=${encodeURIComponent(formData.pesel)}&phoneNr=${encodeURIComponent(formData.phone)}`);

                // Fetch the client
                const clientResponse = await fetch(`http://localhost:8090/api/client/pesel?pesel=${encodeURIComponent(formData.pesel)}`);
                const clientData = await clientResponse.json();

                if (!clientData.userId) {
                    throw new Error("Client ID is missing");
                }

                setClient(clientData);
                console.log("Client Data:", clientData);

                // Fetch the service
                const serviceResponse = await fetch(`http://localhost:8090/api/service/id?id=${encodeURIComponent(formData.service)}`);
                const serviceData = await serviceResponse.json();

                if (!serviceData.serviceId) {
                    throw new Error("Service ID is missing");
                }

                setService(serviceData);
                console.log("Service Data:", serviceData);

                // Book the visit using local variables for clientData and serviceData
                const bookVisitResponse = await fetch(`http://localhost:8090/api/visit/book?clientId=${encodeURIComponent(clientData.userId)}&serviceId=${encodeURIComponent(serviceData.serviceId)}&visitId=${encodeURIComponent(eventBook.id)}`);

                if (!bookVisitResponse.ok) {
                    throw new Error("Failed to book visit");
                }else {
                    setEventBook(null)
                    setEventClicked(null)
                    // Set alert
                    setAlertOpened(true);
                    setAlertData({
                        message: 'Wizyta zostaa umwiona',
                        color: 'grey'
                    });
                    fetchVisits();
                }
            } catch (error) {
                console.error('Error:', error);
                // Handle error
            }
        }
    };

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData({
            ...formData,
            [name]: value,
        });
    };

    const handleCancelClick = () =>{
         setEventCancel(eventClicked);
    }

    const handleAnulujClick= () =>{
        setEventCancel({})
    }

    const handleDeleteEvent = () =>{
        fetch(`http://localhost:8090/api/visit/cancel-visit?visitId=${eventCancel.id}`)
            .then(()=>{
                setEventCancel(null);
                setEventClicked(null)
                fetchVisits();
        })
            .catch()
    }

    return (
        <>
            <div className="calendar">
                <DnDCalendar
                    defaultDate={moment().toDate()}
                    defaultView="week"
                    events={events}
                    localizer={localizer}
                    onEventDrop={onEventDrop}
                    onEventResize={onEventResize}
                    resizable
                    onSelectEvent={handleEventClick}
                    eventPropGetter={eventPropGetter}
                    min={new Date(2024, 0, 1, 8, 0)}
                    max={new Date(2024, 0, 1, 20, 0)}
                />
            </div>
            {eventClicked && (
                <div className="overlay">
                    <div className="event-calendar-open">
                        <div>
                            <FontAwesomeIcon icon={faTimes} style={{ cursor: 'pointer' }} onClick={closeOverlay} />
                        </div>
                        <div className={eventClicked.title === "Wolny termin" ? 'visit-service-name free-appoint' : 'visit-service-name'}>{eventClicked.title}</div>
                        <div>Rozpoczcie wizyty: {eventClicked.start.toLocaleTimeString([], { timeStyle: 'short' })}</div>
                        <div>Zakoczenie wizyty: {eventClicked.end.toLocaleTimeString([], { timeStyle: 'short' })}</div>
                        {JSON.parse(localStorage.getItem('userType')) === "DR" && eventClicked.end < (new Date()) && (
                            <>
                                <textarea placeholder="Wnioski i zalecenia..." defaultValue={eventClicked.description} name='description' className='textarea-visit-description' onChange={handleTextareaChange} />
                                <button className='large-button' onClick={handleSubmit}>Zatwierd</button>
                            </>
                        )}
                        {JSON.parse(localStorage.getItem('userType')) === "RE" && eventClicked.title === 'Wolny termin' && (
                            <div>Rehabilitant: {eventClicked.employee.name} {eventClicked.employee.surname}</div>
                        )}
                        {JSON.parse(localStorage.getItem('userType')) === "RE" && eventClicked.client && (
                            <div>Pacjent: {eventClicked.client.name} {eventClicked.client.surname}</div>
                        )}
                        {eventBook && (
                            <div className='form-booking'>
                                <div>
                                    <label>Imi</label>
                                    <input placeholder='Imi' type="text" name="name" value={formData.name}
                                           onChange={handleChange}
                                           required/>
                                </div>
                                <div>
                                    <label>Nazwisko</label>
                                    <input placeholder='Nazwisko' type="text" name="surname" value={formData.surname}
                                           onChange={handleChange} required/>
                                </div>
                                <div>
                                    <label>Numer telefonu</label>
                                    <input placeholder='Numer telefonu' type="tel" name="phone"
                                           value={formData.phone} onChange={handleChange}
                                           required/>
                                </div>
                                <div>
                                    <label>PESEL</label>
                                    <input placeholder='PESEL' type="text" name="pesel"
                                           value={formData.pesel} onChange={handleChange}
                                           required/>
                                </div>
                                <div>
                                    <label>Wykonywany zabieg</label>
                                    <select name="service" value={formData.service} onChange={handleChange} required>
                                        <option value="">Wybierz zabieg</option>
                                        {services.map((service, index) => (
                                            <option key={index} value={service.serviceId}>{service.name}</option>
                                        ))}
                                    </select>
                                </div>
                            </div>
                        )}
                        {JSON.parse(localStorage.getItem('userType')) === "RE" && eventClicked.title === 'Wolny termin' && (
                            <>
                                <button className='large-button width-90'
                                        onClick={handleBookClick}>{eventBook ? "Zatwierd" : "Umw wizyt"}</button>
                            </>
                        )}
                        {eventCancel!==eventClicked && JSON.parse(localStorage.getItem('userType')) === "RE" &&
                            eventClicked.client && eventClicked.start > new Date() && (
                            <>
                                <button className='large-button'
                                        onClick={handleCancelClick}>Odwoaj wizyt</button>
                            </>
                        )}
                        {eventCancel===eventClicked && JSON.parse(localStorage.getItem('userType')) === "RE" && eventClicked.client && (
                            <div className='confirm-div'>
                                <div> Czy na pewno chcesz odwoa t wizyt?</div>
                                <div className='width-30'>
                                        <button className='add-button red'
                                                onClick={handleAnulujClick}>Anuluj</button>
                                        <button className='add-button white'
                                                onClick={handleDeleteEvent}>Tak
                                        </button>
                                </div>
                            </div>
                        )}
                    </div>
                </div>
            )}
        </>
    );
};

export default EmpCalendar;
