import React, { useState} from 'react';

function Row({ idField, array, properties, handleInputChange, setMod, handleEditConfirm, handleDeleteConfirm }) {
    const [menuOpened, setMenuOpened] = useState(null);
    const [editMode, setEditMode] = useState({});
    const [deleteMode, setDeleteMode] = useState({});


    const handleEdit = (e, item) => {
        e.stopPropagation();
        setDeleteMode({});
        setEditMode(editMode !== item ? item : {});
        setMod(item);
    };

    const handleDeleteClick = (e, item) => {
        e.stopPropagation();
        setEditMode({});
        setDeleteMode(deleteMode !== item ? item : {});
    };

    const handleDownIconClick = (index) => {
        setMenuOpened(menuOpened !== index ? index : null);
        if(editMode) setEditMode({})
        if(deleteMode) setDeleteMode({})
    };


    return (
        <>
            {array.sort((a, b) => a[idField] - b[idField]).map((item, index) => (
                <div className='row' key={index}  >
                    <div className='row-up admin' onClick={() => handleDownIconClick(index)}>
                        {properties.map((prop, propIndex) => (
                            <div key={propIndex}>{item[prop]}</div>
                        ))}
                    </div>
                    {menuOpened === index && (
                        <>
                            <div className='down'>
                                <div onClick={(e) => handleEdit(e, item)}>Edytuj dane</div>
                                <div className='row-menu' onClick={(e) => handleDeleteClick(e, item)}>Usun</div>
                            </div>
                            {editMode === item && (
                                <form className='adding-employee-form no-border' onSubmit={(e) => {
                                    e.preventDefault();
                                    handleEditConfirm(item[idField]);
                                }}>
                                    {properties.map((prop, propIndex) => (
                                        <input key={propIndex} type="text" name={prop} onChange={handleInputChange(setMod)}
                                               defaultValue={item[prop]} />
                                    ))}
                                    <button className='add-button' type='submit'>Zapisz</button>
                                </form>
                            )}
                            {deleteMode === item && (
                                <form className='adding-employee-form no-border row-dir' >
                                    <div>Czy chcesz na stae usun ten element z bazy danych?</div>
                                    <div className='button-pair'>
                                        <button className='add-button white ' onClick={(e) => {
                                            e.stopPropagation();
                                            setDeleteMode({});
                                        }}>Anuluj</button>
                                        <button onClick={(e) => {
                                            e.preventDefault();
                                            e.stopPropagation();
                                            handleDeleteConfirm(item[idField]);
                                        }} className='add-button red'>Tak</button>
                                    </div>
                                </form>
                            )}
                        </>
                    )}
                </div>
            ))}
        </>
    );
}

export default Row;
