import React, {useContext, useEffect, useState} from "react";
import {DataContext} from "../App";

function ClientPersonalData() {

    const { setAlertOpened, setAlertData, setProfileOpened, setShowNavbar } = useContext(DataContext);

    const [userData, setUserData] = useState({});
    const [editMode, setEditMode] = useState(false);
    const [accountDelete, setAccountDelete] = useState(false);

    useEffect(() => {
        setUserData(JSON.parse(localStorage.getItem('userData')));
    }, []);

    const handleEditDataClick = () => {
        setEditMode(true);
    };


    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setUserData(prevData => ({
            ...prevData,
            [name]: value
        }));
    };

    const handleSaveDataClick = () => {

        fetch(`http://localhost:8090/api/client/edit?clientId=${encodeURIComponent(userData.userId)}&name=${encodeURIComponent(userData.name)}&surname=${encodeURIComponent(userData.surname)}&email=${encodeURIComponent(userData.email)}&pesel=${encodeURIComponent(userData.pesel)}&phoneNr=${encodeURIComponent(userData.phoneNr)}`)
            .then(() => {
                // Client added successfully
                console.log("Client data edited successfully");
            })
            .catch(error => {
                console.log("Error editing client data", error);
            });

        setAlertOpened(true);
        setAlertData({
            message: "Dane osobowe zostay zaktualizowane pomylnie",
            color: "grey"
        })

        localStorage.setItem('userData', JSON.stringify(userData));
        setEditMode(false);
    };

    const deleteAccount = () =>{
        setAccountDelete(true);
    }

    const handleAnulujClick = () =>{
        setAccountDelete(false);
    }

    const confirmDelete = () =>{
        fetch(`http://localhost:8090/api/client/delete?id=${userData.userId}`)
            .then(()=>{
                setProfileOpened(false);
                setShowNavbar(true);
            })
            .catch()
    }

    return (
        <div className='personal-data'>
            <div className='user-data'>
                <div className='info-group'>
                    <div>Imi</div>
                    {editMode ? (
                        <input type="text" name="name" value={userData.name} onChange={handleInputChange}/>
                    ) : (
                        <div>{userData.name}</div>
                    )}
                </div>
                <div className='info-group'>
                    <div>Nazwisko</div>
                    {editMode ? (
                        <input type="text" name="surname" value={userData.surname} onChange={handleInputChange}/>
                    ) : (
                        <div>{userData.surname}</div>
                    )}
                </div>
                <div className='info-group'>
                    <div>Adres e-mail</div>
                    {editMode ? (
                        <input type="email" name="email" value={userData.email} onChange={handleInputChange}/>
                    ) : (
                        <div>{userData.email}</div>
                    )}
                </div>
                <div className='info-group'>
                    <div>Numer telefonu</div>
                    {editMode ? (
                        <input type="tel" name="phoneNr" value={userData.phoneNr} onChange={handleInputChange}/>
                    ) : (
                        <div>{userData.phoneNr}</div>
                    )}
                </div>
                <div className='info-group'>
                    <div>PESEL</div>
                    {editMode ? (
                        <input type="text" name="pesel" value={userData.pesel} onChange={handleInputChange}/>
                    ) : (
                        <div>{userData.pesel}</div>
                    )}
                </div>
            </div>
            <div className='button-group'>
                <button onClick={deleteAccount}>Usu konto</button>
                {editMode ? (
                    <button onClick={handleSaveDataClick}>Zapisz dane</button>
                ) : (
                    <button onClick={handleEditDataClick}>Edytuj dane</button>
                )}
            </div>
            {accountDelete && (
                <div className='confirm-div width-80'>
                    <div> Czy na pewno chcesz usun swoje konto?</div>
                    <div className='width-30'>
                        <button className='add-button red '
                                onClick={(e) => handleAnulujClick(e)}>Nie
                        </button>
                        <button className='add-button white'
                                onClick={confirmDelete}>Tak
                        </button>
                    </div>
                </div>
            )}
        </div>
    )
}

export default ClientPersonalData