import React, { useContext, useState } from "react";
import { DataContext } from "../App";
import Row from "./Row";
import SearchBar from "./SearchBar";

function AdminServiceMod() {
    const { setAlertOpened, setAlertData, services, fetchServices } = useContext(DataContext);

    const [addingForm, setAddingForm] = useState(false);
    const [formInputs, setFormInputs] = useState({
        name: '',
        price: 0
    });

    const [serviceMod, setServiceMod] = useState({});

    const [filteredServices, setFilteredServices] = useState([]); // State for filtered services

    const handleSearch = (query) => {
        const filtered = services.filter(service => {
            const name = service.name.toLowerCase();
            const price = service.price.toString().toLowerCase();
            return name.includes(query) || price.includes(query);
        });
        setFilteredServices(filtered);
    };

    const handleAddServiceClick = () => {
        setAddingForm(!addingForm);
    };

    const handleAddingFormSubmit = (e) => {
        e.preventDefault();
        console.log(formInputs);

        let exists=false

        services.forEach(service => {
            if(service.name===formInputs.name) exists=true;
        })
        if(!exists) {
            fetch(`http://localhost:8090/api/service/add?name=${formInputs.name}&price=${formInputs.price}`)
                .then(() => {
                    setFormInputs({
                        name: '',
                        price: 0
                    });
                    setAlertOpened(true);
                    setAlertData({
                        message: "Zabieg zosta dodany pomylnie",
                        color: 'grey'
                    });
                    fetchServices();
                })
                .catch(error => console.error("Error adding new service", error));
        }else{
            setAlertOpened(true);
            setAlertData({
                message: "Zabieg ju istnieje w bazie danych",
                color: "darkred"
            });
        }
    };

    const handleInputChange = (setState) => (e) => {
        const { name, value } = e.target;
        setState((prevState) => ({
            ...prevState,
            [name]: value,
        }));
    };

    const handleEditService = (id) => {
        fetch(`http://localhost:8090/api/service/edit?serviceId=${id}&name=${serviceMod.name}&price=${serviceMod.price}`)
            .then(() => {
                setServiceMod({});
                setAlertOpened(true);
                setAlertData({
                    message: "Zabieg zosta zaktualizowany pomylnie",
                    color: 'grey'
                });
                fetchServices();
            })
            .catch(error => console.error("Error editing service", error));
    };

    const handleDeleteService = (id) => {
        fetch(`http://localhost:8090/api/service/delete?id=${id}`)
            .then(() => {
                setAlertOpened(true);
                setAlertData({
                    message: "Zabieg zosta usunity pomylnie",
                    color: 'grey'
                });
                fetchServices();
            })
            .catch(error => console.error("Error adding new employee", error));
    };

    return (
        <div className='admin-panel'>
            <div className="admin-panel-top">
                <SearchBar onSearch={handleSearch} />
                <button className='button new-button' onClick={handleAddServiceClick}>Nowy zabieg</button>
            </div>
            {addingForm && (
                <form className='adding-service-form' onSubmit={handleAddingFormSubmit}>
                    <div>
                        <label>Nazwa zabiegu</label>
                        <input name='name' onChange={handleInputChange(setFormInputs)} value={formInputs.name} type="text" required />
                    </div>
                    <div>
                        <label>Cena</label>
                        <input name='price' onChange={handleInputChange(setFormInputs)} value={formInputs.price} type="number" required />
                    </div>
                    <button className='add-button' type='submit'>Zatwierd</button>
                </form>
            )}
            <Row
                array={filteredServices.length > 0 ? filteredServices : services}
                idField="serviceId"
                properties={['name', 'price']}
                handleEditConfirm={handleEditService}
                handleInputChange={handleInputChange}
                serviceMod={serviceMod}
                setMod={setServiceMod}
                handleDeleteConfirm={handleDeleteService}
            />
        </div>
    );
}

export default AdminServiceMod;
